import { useMutation, useQueryClient } from "@tanstack/react-query";
import { useState, ChangeEvent } from "react";
import { useNavigate } from "react-router-dom";
import Study from "../../../../../di/Study";
import debounce from "../../../../../utils/debounce";
import CreateStudyButton from "./CreateStudyButton";

export interface IErrorMessage {
  studyNameMessage: string;
  repoNameMessage: string;
}

type CreateStudyProps = {
  studyName: string;
  repoName: string;
};

export default function CreateStudyButtonContainer() {
  const queryClient = useQueryClient();
  const mutation = useMutation(
    ({ studyName, repoName }: CreateStudyProps) => Study.createStudy(studyName, repoName),
    {
      onSuccess: (data) => {
        queryClient.invalidateQueries(["studyList"]);
        navigate(`/study/${data.id}`);
      },
    }
  );
  const [studyName, setStudyName] = useState<string>("");
  const [repoName, setRepoName] = useState<string>("");
  const [errorMessage, setErrorMessage] = useState<IErrorMessage>({
    studyNameMessage: "",
    repoNameMessage: "",
  });

  const checkStudyName = debounce((name: string) => {
    const message = "  2~10  .";

    if (name.length < 2 || name.length > 10) {
      setErrorMessage({
        ...errorMessage,
        studyNameMessage: message,
      });
      return;
    }

    setErrorMessage({
      ...errorMessage,
      studyNameMessage: "",
    });
  });

  const checkRepoName = debounce((name: string) => {
    const message1 = "  1   .";
    const message2 = "    _, -   .";
    const pattern = /[-|-|-~!@#$%^&*()+|<>?:{}]/;

    if (!name) {
      setErrorMessage({
        ...errorMessage,
        repoNameMessage: message1,
      });
      return;
    }

    if (pattern.test(name)) {
      setErrorMessage({
        ...errorMessage,
        repoNameMessage: message2,
      });
      return;
    }

    setErrorMessage({
      ...errorMessage,
      repoNameMessage: "",
    });
  });

  const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;

    if (name === "studyName") {
      setStudyName(value);
      checkStudyName(value);
      return;
    }

    setRepoName(value.trim());
    checkRepoName(value);
  };

  const navigate = useNavigate();

  const handleSubmit = async () => {
    mutation.mutate({ studyName, repoName });
  };

  return (
    <CreateStudyButton
      studyName={studyName}
      repoName={repoName}
      errorMessage={errorMessage}
      handleSubmit={handleSubmit}
      handleChange={handleChange}
    />
  );
}
